body{
    font-size: 20px;
}

a{
    text-decoration: none;
    color:inherit;
    line-height: normal;
}

body,html{
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    overflow: auto;
    overflow: initial;
   
}

html{
    scroll-behavior: smooth;
}

button{
    border: none;
    outline: none;
}

button:hover{
    border: none;
    outline: none;
    cursor: pointer;
}

@media (max-width: 960px){
    body{
        font-size: 16px;
    }
}