.menu-main-background{
    height: 100vh;
    color: white;
    background:linear-gradient( rgba(0, 0, 0, 0.644) 100%, rgba(0, 0, 0, 0.5)100%), url('../assets/menu.jpg');
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    -o-background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
}

.menu-list{
    font-size: 0.9em;
}

.menu-list-container{
    padding: 40px;
}

.list-category{
    border-bottom: 1px solid white;
    margin-bottom: 10px;
}

.list-category:last-child{
    border: none;
}