.m-center{
    display: -webkit-box;      
    display: -moz-box;         
    display: -ms-flexbox;      
    display: -webkit-flex;     
    display: flex;
    align-items: center;
    justify-content: center;
}

.m-center-text{
    text-align: center;
}

.m-nav{
    width: 100vw;
    height: 80px;
    z-index: 5;
    color: white;
    position: fixed;
    top: 0;
    display: -webkit-box;      
    display: -moz-box;         
    display: -ms-flexbox;      
    display: -webkit-flex;     
    display: flex;
    align-items: center;
    justify-content: space-between;
}


.m-nav ul li{
    display: inline-block;
    list-style: none;
}

.m-nav ul li a{
    padding: 10px;
    position: relative;
}

.m-nav ul li a:hover{
    cursor: pointer;
}

.m-nav ul li a::after,
.m-nav ul li a::before{
    content: "";
    transition: all .5s;
    background-color: white;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transform: scaleX(0);
}

.m-nav ul li a::before{
    top: 0;
    transform-origin: left;
}

.m-nav ul li a::after{
    bottom: 0;
    transform-origin: right;
}

.m-nav ul li a:hover::after,
.m-nav ul li a:hover::before{
    transform: scaleX(1);
}

.m-cart{
    position: relative;
    margin-right: 25px;
    padding: 20px;
    display: -webkit-box;      
    display: -moz-box;         
    display: -ms-flexbox;      
    display: -webkit-flex;     
    display: flex;
    align-items: center;
    justify-content: center;
}

.m-cart:hover{
    cursor: pointer;
}

.m-cart .cart-count{
    background-color: rgb(231, 63, 63);
    position: absolute;
    text-align: center;
    width: 1em;
    height: 1em;
    line-height: 1em;
    border-radius: 50%;
    bottom: 0;
    right: 0;
}


.m-white-border{
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}

.m-box-shadow-top{
    -moz-box-shadow:    inset 0 35px 25px #000000;
    -webkit-box-shadow: inset 0 35px 25px #000000;
     box-shadow:         inset 0 35px 25px #000000;
}

.m-left-text{
    text-align:left;
    padding: 10px;
}

.m-margin-right{
    margin-left: 6%;
}

.m-btn{
    font-size: 1em;
    padding: 10px;
    border-radius: 30px;
    transition: background-color 0.5s linear;
}

.m-btn.sm{
    width: 100px;
}

.m-btn.md{
    width: 250px;
}


.m-btn.xl{
    width: 400px;
}


.m-main-text{
    font-size: 1.3em;
    width: calc(40*(1.3em/1.618));
    line-height: calc(1.3em * 1.618);
}

.m-grid{
    display: grid;
    grid-template-columns: 250px 250px 250px;
    grid-template-rows: auto;
    grid-gap: 20px;
}

.m-card{
    width: 250px;
    font-size: 0.9em;
    text-align: center;
    transition: all 0.2s linear;
}

.m-card button{
    margin-bottom: 5px;
}

.m-card img{
    display: block;
}

.m-card h4{
    margin: 0;
}

.m-grid .m-card:hover{
    transform: scale(1.1)
}

.m-sideBar{
    position: fixed;
    top: 0px;
    z-index: 7;
    transition: right 0.5s linear;
    right: -400px;
    height: 100vh;
    width: 400px;
}

.m-sideBar .arrow-icon{
    position: absolute;
    transition: all 0.3s ease-in;
    top: 10px;
    left: 10px;
}

.m-sideBar .bag-icon{
    position: absolute;
    font-size: 2.5em;
    left: 50%;
    top: 50%;
}

.m-sideBar .arrow-icon:hover{
    cursor: pointer;
    transform: scale(1.3);
}


.m-sideBar ul{
    list-style: none;
    margin-top: 80px;
    padding: 0;
}

.m-sideBar ul li{
    padding: 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.281);
}

.scale-animation{
    -webkit-animation: scale 0.3s ease-in;
    -moz-animation: scale 0.3s ease-in;
    -o-animation: scale 0.3s ease-in;
    animation: scale 0.3s ease-in;
}

.opacity-animation{
    -webkit-animation: opacity 1s linear;
    -moz-animation: opacity 1s linear;
    -o-animation: opacity 1s linear;
    animation: opacity 1s linear;
}

@keyframes scale{
    0%{
        transform: scale(1.2);
    }

    50%{
       transform: scale(1.5);
    }

    100%{
        transform: scale(1);
    }
}

@keyframes opacity{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
} 

@media (max-width: 900px){
    .m-nav{
        font-size: 0.9em;
    }
    
    .m-left-text{
        text-align: center;
    }

    .m-grid{
        grid-template-columns: 250px;
    }

    .m-sideBar{
        width: 250px;
        right: -250px;
    }
    .m-sideBar ul{
        font-size: 0.8em;
    }

    .m-margin-right{
        margin:0
    }

    .m-btn.xl{
        width: 250px;
    }

    .m-center.home-contact{
        flex-direction: column;
    }

    .m-main-text{
        font-size: 1em;
        line-height: calc(1em * 1.618);
        width: 300px;
    }
}
