.home-main-background{
   height: 100vh;
   color: white;
   background:linear-gradient( rgba(0, 0, 0, 0.5) 100%, rgba(0, 0, 0, 0.5)100%), url('../assets/homeBackground.jpg');
   -webkit-background-size: cover;
   -moz-background-size: cover;
   background-size: cover;
   -o-background-size: cover;
   background-position: center;
   background-repeat: no-repeat; 
}

.home-main-background a{
    display: inline-block;
}

.home-intermediate{
    height: 100vh;
    color: white;
    background:linear-gradient( rgba(0, 0, 0, 0.5) 100%, rgba(0, 0, 0, 0.5)100%), url('../assets/drinks.jpg');
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    -o-background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
}

.home-email{
    height: 100vh;
    color: white;
    background:linear-gradient( rgba(0, 0, 0, 0.5) 100%, rgba(0, 0, 0, 0.5)100%), url('../assets/barrels.jpeg');
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    -o-background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
}

.home-contact{
    padding: 20px;
    height: 50vh;
}

.contact-info{
    border: 1px solid white;
    width: 400px;
    min-width: 150px;
    padding: 20px;
}

.contact-info:first-child{
    margin-right: 10px;
}

.contact-info header{
    display: inline-block;
    height: 0px;
    background-color: thistle;
    margin-right: 10px;
}

.home-input{
    margin-bottom: 20px;
    padding: 10px;
    width: 400px;
}


@media (max-width: 960px){
    
    .home-input{
        width: 250px;
    }

    .m-center.home-contact{
        flex-direction: column;
    }

    .contact-info{
        width: 250px;
    }
    
    .contact-info:first-child{
        margin-right: 0;
        margin-bottom: 5px;
    }
}